import React from 'react';
import Home from './pages/Home';
import Doc from './pages/Doc';
import Tuto from './pages/Tuto';
import Blog from './pages/Blog';
import Comm from './pages/Comm';
import Login from './pages/Login';

// import DocContent from './pages/DocContent';

const Core = () => <h1>Core</h1>
const Guide = () => <h2>Guide</h2>
const Api = () => <h3>Api</h3>
const Hooks = () => <h4>Hooks</h4>

const routes = [
    {
        path: '/',
        exact: true,
        component: Home
    },
    {
        path: '/doc',
        component: Doc,
        pri: true,
        routes: [
            {
                path: '/doc/core',
                component: Core,
            },
            {
                path: '/doc/guide',
                component: Guide,
            },
            {
                path: '/doc/api',
                component: Api,
            },
            {
                path: '/doc/hooks',
                component: Hooks,
            },
        ]
    },

    {
        path: '/tuto',
        component: Tuto,
        pri: true
    },

    {
        path: '/blog',
        component: Blog
    },

    {
        path: '/comm',
        component: Comm
    },

    {
        path: '/login',
        component: Login
    },
]

export default routes;